<template>
  <BlockStructure v-bind="$props">
    <template #body>
      <div class="mediafolder-link">
        <form
          v-if="!block.mediafolder"
          method="POST"
          :action="`${block['@id']}/@@add-and-link-mediafolder`"
        >
          <input type="hidden" name="_authenticator" :value="sl.authToken" />
          <button type="submit" class="btn btn-success btn-sm">
            Click here to create a new Media Folder
          </button>
        </form>
        <a
          v-else
          :href="block.mediafolder['@id']"
          class="btn btn-success btn-sm"
          >Go the the referenced Media Folder</a
        >
      </div>
      total {{ data.items_total }}
      <div class="table-responsive">
        <table class="table table-hover">
          <thead>
            <tr>
              <th>Type</th>
              <th>Title</th>
              <th>Size</th>
              <th>Modified</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="file in data.items" :key="file.UID">
              <template v-if="file['@type'] === 'Image'">
                <td>{{ file.image["content-type"] }}</td>
                <td>
                  <a :href="file.image.download">{{ file.title }}</a>
                </td>
                <td>{{ file.image.size }}</td>
              </template>
              <template v-if="file['@type'] === 'File'">
                <td>{{ file.file["content-type"] }}</td>
                <td>
                  <a :href="file.file.download">{{ file.title }}</a>
                </td>
                <td>{{ file.file.size }}</td>
              </template>
              <td>{{ file.modified }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <Pagination
        v-if="data.batching"
        @next="fetchNext"
        @previous="fetchPrevious"
        :batching="data.batching"
      />
    </template>
  </BlockStructure>
</template>
<script>
import { useSimplelayoutStore } from "@/store.js";
import BlockStructure from "@/components/standard/BlockStructure.vue";
import Pagination from "@/components/Pagination.vue";

export default {
  components: {
    BlockStructure,
    Pagination,
  },
  props: {
    actions: {
      type: Array,
      required: true,
    },
    rowIndex: {
      type: Number,
      required: true,
    },
    columnIndex: {
      type: Number,
      required: true,
    },
    blockIndex: {
      type: Number,
      required: true,
    },
    block: {
      type: Object,
      required: true,
    },
  },
  setup() {
    const sl = useSimplelayoutStore();
    return { sl };
  },
  data() {
    return {
      data: { items: [], batching: null },
    };
  },
  created() {
    this.fetchData();
  },
  watch: {
    "block.modified"() {
      this.fetchData();
    },
  },
  methods: {
    async fetchData() {
      const response = await this.axios.get(this.block.computed_query);
      this.data = response.data;
    },
    async fetchNext(url) {
      const response = await this.axios.get(url);
      this.data = response.data;
    },
    async fetchPrevious(url) {
      const response = await this.axios.get(url);
      this.data = response.data;
    },
  },
};
</script>
